import logo from './logo.svg';
import './App.css';
import toast, { Toaster } from 'react-hot-toast';
import Layout from './Components/Layout/Layout';
import Home from './Components/Home/Home';
import Cart from './Components/Cart/Cart';
import Products from './Components/Products/Products';
import Register from './Components/Register/Register';
import Login from './Components/Login/Login';
import About from './Components/About/About';
import Categorie from './Components/Categorie/Categorie';
import NotFound from './Components/NotFound/NotFound';
import { Navigate, RouterProvider, createBrowserRouter, createHashRouter } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { jwtDecode } from 'jwt-decode';
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute';
import ProductDetails from './Components/ProductDetails/ProductDetails';
import { CartContextProvider } from './Context/CartContext';
import Checkout from './Components/Checkout/Checkout'
import { WishListContextProvider } from './WishList/WishListContext';
import Wishlist from './Components/Wishlist/Wishlist';
import { Offline, Online } from "react-detect-offline";
import ResetPass from './Components/ResetPass/ResetPass';
import ForgetPass from './Components/ForgetPass/ForgetPass';
import AllOrders from './Components/AllOrders/AllOrders';

function App() {
  // const [userData, setuserData] = useState(null)
  // function saveUserData() {
  //   let encodedToken = localStorage.getItem("userToken");
  //   let decodedToken = jwtDecode(encodedToken);
  //   setuserData(decodedToken);
 
  // }

  // function ProtectedRoute(props) {

  //   if(localStorage.getItem('userToken')== null){
  //     return <Navigate to={'/Login'} />
  //   }
  //   else{
  //      return props.children;
  //   }
  // }
  
  // function logOut() {
  //   localStorage.removeItem("userToken")
  //   setuserData(null)
  //   return <Navigate to={'./Login'} />
  // }
  useEffect(()=>{
    if(localStorage.getItem("userToken")!== null){
      saveUserData()
    }
  },[])
  const [userData, setuserData] = useState(null)
  useEffect(()=>{
    if(localStorage.getItem("userToken")){
     let data = jwtDecode(localStorage.getItem("userToken"))
     setuserData(data)
    }
  },[])
  function saveUserData(data){
    setuserData(data)
  }
  function logOut(){
    localStorage.removeItem("userToken")
    setuserData(null)
    return <Navigate to ='/Login'/>
  }
  function ProtectedRoute(props){
    if(localStorage.getItem('userToken')){
      return props.children
    }
    else{
      return <Navigate to ='/Login'/>
    }
  }
 
 

  let routers = createHashRouter([
    {
      path: '', element: <Layout setuserData={setuserData} userData={userData} logOut={logOut}  />, children: [
        { index: true, element: <ProtectedRoute><Home saveUserData={saveUserData}/></ProtectedRoute> },
        { path: 'Products', element: <ProtectedRoute><Products /></ProtectedRoute> },
        { path: 'Categories', element: <ProtectedRoute><Categorie /></ProtectedRoute> },
        { path: 'ProductDetails/:id', element: <ProtectedRoute><ProductDetails /></ProtectedRoute> },
        { path: 'About', element: <ProtectedRoute><About /></ProtectedRoute> },
        { path: 'Cart', element: <ProtectedRoute> <Cart /></ProtectedRoute> },
        { path: 'wishList', element: <ProtectedRoute> <Wishlist /></ProtectedRoute> },
        { path: 'Checkout', element: <ProtectedRoute><Checkout /></ProtectedRoute> },
        { path: 'allorders', element: <ProtectedRoute><AllOrders userData={userData}/></ProtectedRoute> },
        { path: 'Login', element: <Login  saveUserData={saveUserData}/> },
        { path: 'Register', element: <Register saveUserData={saveUserData} /> },
        { path: 'RestPass', element: <ResetPass/> },
        { path: 'ForgetPass', element: <ForgetPass/> },
        { path: '*', element: <NotFound /> },


      ]
    }
  ])
  return <WishListContextProvider>
    <CartContextProvider>
      <Toaster />
      <Offline>
        <div className='network fw-bold  text-center'><i className="fa-solid fa-wifi fa-sm text-white mx-2"> !</i> Your're offline </div>
      </Offline>
      <RouterProvider router={routers}></RouterProvider>
    </CartContextProvider>
  </WishListContextProvider>
}

export default App;
