import React, { useContext, useEffect, useState } from 'react'
import style from './Products.module.css'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { cartContext } from '../../Context/CartContext'
import toast from 'react-hot-toast'
import { WishListContext } from '../../WishList/WishListContext'
import { Helmet } from "react-helmet";

export default function Products() {

  const [products, setproducts] = useState([])
  const [isLoading, setisLoading] = useState(false)
  let { addToCart, setnumOfCartItems } = useContext(cartContext);
  let { addToWishList, setcount } = useContext(WishListContext);

  async function addProduct(productId) {
    let response = await addToCart(productId);
    if (response?.data?.status == 'success') {
      setnumOfCartItems(response.data.numOfCartItems);
      toast.success(response.data.message, { duration: 2000 })
    }
    else {
      toast.error('Error', { duration: 2000 })
    }

  }
  async function getProducts() {
    setisLoading(true)
    let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/products`);
    setproducts(data.data)
    setisLoading(false)
  }

  async function addFav(productId) {
    let response = await addToWishList(productId);
    if (response?.data?.status === 'success') {
      setcount(response.data.data.length);
      toast.success(response.data.message, { duration: 2000 })
    }
    else {
      toast.error('Error', { duration: 2000 })
    }
  }
  useEffect(() => {
    getProducts();
  }, [])

  return <>
    <Helmet>
      <title>FreshCart/Products</title>
    </Helmet>
    {isLoading ? <div className='loading '>
      <i className="text-main fa-solid fa-spinner fa-spin fa-2x"></i>
    </div> : <> <div className="row mt-5">
      {products.map((product) => <div key={product._id} className='col-md-2'>
        <div className="product cursor-pointer px-2 py-3 " >
          <Link to={'/ProductDetails/' + product._id} >

            <img className='w-100' src={product.imageCover} />
            <span className='text-main fw-bold font-sm'>{product.category.name}</span>
            <h3 className='h6 fw-bolder'>{product.title.split(' ').slice(0, 2).join(' ')}</h3>
            <div className='d-flex justify-content-between'>
              <span className='text-muted'>{product.price} EGP</span>
              <span>
                <i className='fas fa-star rating-color'></i>
                {product.ratingsAverage}
              </span>
            </div>
          </Link>
          <div className='d-flex justify-content-between'>
            <button onClick={() => addFav((product._id))} className='btn '><i className='fa-solid fa-heart-circle-plus text-danger fa-lg '> </i></button>
            <button onClick={() => addProduct((product._id))} className='btn bg-main text-white'>+Add</button>
          </div>
        </div>

      </div>)}
    </div>
    </>}


  </>
}

