import React, { useContext, useEffect, useState } from 'react'
import style from './ProductDetails.module.css'
import { useParams } from 'react-router-dom'
import axios from 'axios';
import Slider from "react-slick";
import { cartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';
import { WishListContext } from '../../WishList/WishListContext';
import { Helmet } from "react-helmet";


export default function ProductDetails() {
  let {addToCart ,setnumOfCartItems}=useContext(cartContext)
  let {addToWishList,setcount}=useContext(WishListContext);

  const [productDetails, setproductDetails] = useState(null)
  const [isLoading, setisLoading] = useState(false)
  let {id} = useParams();
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
   useEffect(()=>{
    getProductDetails();
  },[])

 async function getProductDetails(){
  setisLoading(true)
    let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`);
    setproductDetails(data.data)
    setisLoading(false)
    // console.log(data.data);
  }
  async  function addProduct(productId){
    let response = await addToCart(productId);
    if(response?.data?.status=='success'){
      setnumOfCartItems(response.data.numOfCartItems);
      toast.success(response.data.message , {duration:2000})
    }
    else{
      toast.error('Error', {duration:2000})
    }
    
  }

  async function addFav(productId){
    let response = await addToWishList(productId);
    if(response?.data?.status === 'success'){
     setcount(response.data.data.length);
     toast.success(response.data.message , {duration:2000})
    }
    else{
     toast.error('Error', {duration:2000})
    }
   }
 

  return <>
    <Helmet>
      <title>FreshCart/ProductDetails</title>
    </Helmet>
   
   {isLoading?<div className='loading '>
   <i  className="text-main fa-solid fa-spinner fa-spin fa-2x"></i>
   </div>: <> <div className="row align-items-center py-3">
     <div className='col-md-4'>
      <Slider {...settings}>
        {productDetails?.images.map((image,e)=><img key={e} src={image}/>)}
     </Slider>
    
     </div>
     <div className='col-md-8'>
      <h3>{productDetails?.title}</h3>
      <p className='text-muted p-2'>{productDetails?.description}</p>
      <div className='d-flex justify-content-between'>
        <span className='text-muted'>{productDetails?.price} EGP</span>
        <div >
          <span className='mx-2'>
            <i className='fas fa-star rating-color'></i>
            {productDetails?.ratingsAverage}
        </span>
        <span onClick={()=>addFav((productDetails._id))} className='mx-2' >
        <i className='fa-solid fa-heart-circle-plus text-danger fa-lg '> </i>
        </span>
        </div>
        
      </div>
      <button onClick={()=>addProduct((productDetails._id))}  className='btn bg-main text-white w-100 mt-3'>+ Add</button>
     </div>
 </div> 
   </>}
  
  
  </>
}

