import React from 'react'
import style from './Footer.module.css'
import amazon from '../../assests/images/amazon.png'
import american from '../../assests/images/american-express.png'
import master from '../../assests/images/pngimg.com - mastercard_PNG1.png'
import payPal from '../../assests/images/pngimg.com - paypal_PNG9.png'
import AppStore from '../../assests/images/app-store-apple-google-play-iphone-mid-autumn-lantern-thumbnail.jpg'
import GoogleStore from '../../assests/images/png-transparent-google-play-store-logo-google-play-app-store-android-wallets-text-label-logo-thumbnail.png'
export default function Footer() {
  return <>
    <footer className='bg-light p-4 mt-3 '>
      <div className='d-flex flex-column  mb-3  p-4'>
      <div className='p-2 border-bottom '>
       <h3 className='fw-bold'>Get The FreshCart App </h3>
       <p className='text-muted'>we will send you a link , open it on your phone to download the app.</p>
        <div className='d-flex   justify-content-between mb-2 flex-sm-column flex-lg-row'>
            <div className='col-lg-10 col-sm-12'><input className='form-control   ' type="email"  placeholder='Email...' /></div>
            <div className='col-lg-2 col-sm-12'><button className='btn bg-main text-white ms-lg-3 ms-sm-0 mt-lg-0 mt-sm-2 w-lg-75 w-sm-100 p-2'>Share App Link</button></div>   
        </div>
      </div>
      <div className='p-2  border-bottom '>
        <div className='d-flex justify-content-between align-items-center'>
           <div><h6 className='fw-bold' >Payment Patners <span className='mx-1'>
              <img src={amazon} width={70} height={70} className='mx-1'/>
              <img src={american} width={70} height={50} className='mx-1'/>
              <img src={master} width={70} height={50} className='mx-1'/>
              <img src={payPal} width={70} height={50}/>

            </span></h6>
            </div>
           <div>
            <h6 className='fw-bold'>Get deliveries with FreshCart <span className='mx-1'>
            <img src={AppStore} width={100} height={30} className='mx-1'/>
            <img src={GoogleStore} width={100} height={30} className='mx-1'/>
            </span>
            </h6>
           </div>
        </div>
      </div>
       
      </div>
    </footer>
  </>
}

