import React from 'react'
import style from './MainSlider.module.css'
import img1 from '../../assests/images/slider-image-3.jpeg'
import img2 from '../../assests/images/slider-image-1.jpeg'
import img3 from '../../assests/images/slider-image-2.jpeg'
import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

export default function MainSlider() {
  return <>
    <div className='row g-0 mb-5 my-3'>
      <div className='col-md-9'>
        <OwlCarousel className='owl-theme' loop items={1}>
          <img height={400} src={img1} className='w-100' />
          <img height={400} src={img2} className='w-100' />
          <img height={400} src={img3} className='w-100' />
        </OwlCarousel>
      </div>
      <div className='col-md-3 '>
        <img height={200} src={img2} className='w-100' />
        <img height={200} src={img1} className='w-100' />
      </div>
    </div>
  </>
}

