import axios from "axios";
import { error } from "jquery";
import { createContext, useEffect, useState } from "react";
export let cartContext = createContext();


export function CartContextProvider(props) {
    const [cartId, setcartId] = useState(null);
    const [numOfCartItems, setnumOfCartItems] = useState(0);


    async function getCart() {
        let response = await getLoggedUsrCart();
        if (response?.data?.status === 'success') {
            setnumOfCartItems(response.data.numOfCartItems);
            setcartId(response.data.data._id);
        }
        // console.log(response);
    }
    useEffect(() => {
        getCart();
    }, []);

    let headers = {
        token: localStorage.getItem('userToken')
    }
    function addToCart(productId) {
        return axios.post(`https://ecommerce.routemisr.com/api/v1/cart`, {
            productId: productId
        }, {
            headers: headers
        }).then((response) => response)
            .catch((error) => error);
    }
    function getLoggedUsrCart() { // function     data    cart 
        return axios.get(`https://ecommerce.routemisr.com/api/v1/cart`,
            {
                headers: headers
            }).then((response) => response)
            .catch((error) => error);
    }
    function removeItem(productId) {
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
            {
                headers: headers
            }).then((response) => response)
            .catch((error) => error);
    }
    function removeAllCart() {
        return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart`,
            {
                headers: headers
            }).then((response) => response)
            .catch((error) => error);
    }
    function updateProductCount(productId, count ) {
        return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${productId}`,
            {
                count: count,

            },
            {
                headers: headers
            }).then((response) => response)
            .catch((error) => error);
    }

    function onlinePayment(cartId, shippingAddress) {
        return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${cartId}?url=http://localhost:3000`,
            {
                shippingAddress: shippingAddress
            },
            {
                headers: headers
            }).then((response) => response)
            .catch((error) => error);
    }



    return <cartContext.Provider value={{removeAllCart,setnumOfCartItems,numOfCartItems,cartId, addToCart, getLoggedUsrCart, removeItem, updateProductCount, onlinePayment }}>
        {props.children}
    </cartContext.Provider>
}