import React, { useContext, useEffect, useState } from 'react'
import style from './Categorie.module.css'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { cartContext } from '../../Context/CartContext'
import toast from 'react-hot-toast'
import { Helmet } from "react-helmet";


export default function Categorie() {
  const [categories, setcategories] = useState([])
  const [isLoading, setisLoading] = useState(false)

  async function getCategories() {
    setisLoading(true)
    let { data } = await axios.get(`https://ecommerce.routemisr.com/api/v1/categories`);
    setcategories(data.data)
    // console.log(data.data);
    setisLoading(false)
  }
  useEffect(() => {
    getCategories();
  }, [])

  return<> 
   <Helmet>
      <title>FreshCart/Categories</title>
    </Helmet>
  {isLoading?<div className='loading '>
  <i  className="text-main fa-solid fa-spinner fa-spin fa-2x"></i>
  </div>: <> <div className="row mt-5 g-2">
   {categories.map((categorie)=><div key={categorie._id} className='col-md-3'>
      <img src={categorie.image} className='w-100 ' height={250}/>
      <h2 className='h5'>{categorie.name}</h2>
   </div>)}
 </div>
  </>}
 

 </>
}

