import React, { useContext, useEffect, useState } from 'react'
import style from './Cart.module.css'
import { cartContext } from '../../Context/CartContext'
import toast from 'react-hot-toast'
import { Link } from 'react-router-dom'
import img from '../../assests/images/cart-empty.jpg'
import { WishListContext } from '../../WishList/WishListContext'
import { Helmet } from "react-helmet";

export default function Cart() {
  let { getLoggedUsrCart, removeItem, updateProductCount, setnumOfCartItems, removeAllCart } = useContext(cartContext);
  let {addToWishList ,setcount}= useContext(WishListContext);
  const [cartDetails, setcartDetails] = useState(null)

  async function getCart() {
    let response = await getLoggedUsrCart();
    if (response?.data?.status === 'success') {
      setnumOfCartItems(response.data.numOfCartItems);
      setcartDetails(response.data.data);
    }
  }

  async function deleteItem(productId) {
    let response = await removeItem(productId);
    setnumOfCartItems(response.data.numOfCartItems);
    setcartDetails(response.data.data);
    toast('prduct removed')
    // console.log(response);
  }

  async function deleteAllCart() {
    let response = await removeAllCart();
    if (response?.data?.message === 'success') {
      setnumOfCartItems(response.data.numOfCartItems);
      setcartDetails(response.data.data);
      toast('All Products is Removed')
      // console.log(response);
    }



  }

  async function updateProductQuntity(productId, count) {
    let response = await updateProductCount(productId, count);
    setcartDetails(response.data.data);
    // toast('prduct count updated ')
    // console.log(response);
    if(count >= 1){
      toast('prduct count updated ')
    }
    else{
      deleteItem(productId)
    }

  }
  async function addFavToCart(productId){
    let response = await addToWishList(productId);
    console.log(response);
    if(response?.data?.status === 'success'){
      setcount(response.data.data.length);
     toast.success(response.data.message , {duration:2000})
    }
    else{
     toast.error('Error', {duration:2000})
    }
   }

  useEffect(() => {
    getCart();
  }, []);


  return <>
    {/* {cartDetails !== null ? <div className='bg-main-light p-4 my-4'>
      <h3>Shop Cart :</h3>
      <h6 className='text-main'>Total Cart Price : {cartDetails?.totalCartPrice} EGP</h6>
      {cartDetails?.products.map((product) =><div key={product?.product._id} className='row align-items-center border-bottom py-2 '>
        <div className='col-md-1'>
          <img src={product?.product.imageCover} className='w-100' />
        </div>
        <div className='col-md-11 d-flex justify-content-between'>
          <div>
            <h6>{product?.product.title}</h6>
            <h6 className='text-main'>Price : {product?.price} EGP</h6>
            <button onClick={() => deleteItem(product?.product._id)} className='btn m-0 p-0'><i className='fa-regular text-main fa-trash-can'></i> Remove</button>
          </div>
          <div>
            <button onClick={() => updateProductQuntity(product?.product._id, product.count + 1)} className='btn border-main btn-sm'>+</button>
            <span className='mx-2'>{product?.count}</span>
            <button onClick={() => updateProductQuntity(product?.product._id, product.count - 1)} className='btn border-main btn-sm'>-</button>
          </div>

        </div>
      </div>
      )}
      <button onClick={deleteAllCart} className='btn btn-danger my-2 mx-2'>Remove All Cart</button>
      <button className='btn bg-main my-2'>
        <Link className='text-white' to={'/Checkout'}>
          Checkout
        </Link>
      </button>
    </div> : <div className='mx-auto'><img src={img} className='w-100' /></div>} */}
     <Helmet>
      <title>FreshCart/Cart</title>
    </Helmet>
    {cartDetails !== null ? <div className='p-4 my-4 table-responsive-md'>
      <h3 className='my-2'>My Cart </h3>
      <table style={{ verticalAlign: "middle" }} className="table   table-bordered my-3 text-center ">
        <thead>
          <tr>
            <th colSpan={2} >Image</th>
            <th colSpan={2}>Name</th>
            <th colSpan={2}>Quantity</th>
            <th colSpan={2}>Price</th>
            <th colSpan={2} >Remove</th>
            <th colSpan={2} >Favorite</th>
          </tr>
        </thead>
        <tbody>
          {cartDetails?.products.map((product) => {
            return <tr key={product?.product._id}>
              <td colSpan={2}><img src={product?.product.imageCover} className='w-75' height={100} /></td>
              <td colSpan={2} ><h6>{product?.product.title}</h6></td>
              <td colSpan={2}>
                <div>
                  <button onClick={() => updateProductQuntity(product?.product._id, product.count + 1)} className='btn border-main btn-sm '>+</button>
                  <span className='mx-2'>{product?.count}</span>
                  <button onClick={() => updateProductQuntity(product?.product._id, product.count - 1 )} className='btn border-main btn-sm '>-</button>
                </div>
              </td >
              <td colSpan={2}><h6 className='text-main'>{product?.price} EGP</h6></td>
              <td colSpan={2}><button onClick={() =>deleteItem(product?.product._id)} className='btn m-0 p-0'><i className='fa-regular text-danger fa-trash-can fa-lg'></i></button></td>
              <td colSpan={2}><button onClick={()=>addFavToCart((product?.product._id))} className='btn m-0 p-0'><i className='fa-solid text-danger fa-heart fa-lg'></i></button></td>
            </tr>

          })}
          <tr className='table-success '>
            <td colSpan={12} className='fw-bold'>Total: {cartDetails?.totalCartPrice} EGP </td>

          </tr>

        </tbody>
      </table>
      <button onClick={deleteAllCart} className='btn btn-outline-danger my-2 mx-2'>Remove All Cart</button>
      <button className='btn bg-main my-2'>
        <Link className='text-white' to={'/Checkout'}>
          Checkout
        </Link>
      </button>
    </div> : <div className='mx-auto'><img src={img} className='w-100' /></div>}



   
  </>
}

