import React, { useContext, useEffect } from 'react'
import style from './Navbar.module.css'
import logo from '../../assests/images/freshcart-logo.svg'
import { Link } from 'react-router-dom'
import { cartContext } from '../../Context/CartContext'
import { WishListContext } from '../../WishList/WishListContext'

export default function Navbar({ userData, logOut }) {
  let { numOfCartItems } = useContext(cartContext);
  let { count } = useContext(WishListContext);

  return <>
    <nav className="navbar fixed-top navbar-expand-sm navbar-light bg-light">
      <div className="container">
        <Link className="navbar-brand" to="/">
          <img src={logo} />
        </Link>
        <button
          className="navbar-toggler d-lg-none"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#collapsibleNavId"
          aria-controls="collapsibleNavId"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="collapsibleNavId">
          {userData !==null ? <ul className="navbar-nav me-auto mt-2 mt-lg-0">
            <li className="nav-item">
              <Link className="nav-link" to="/">Home</Link>
            </li>

            <li className="nav-item">
              <Link className="nav-link" to="Products">Products</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="Categories">Categories</Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" to="allorders"><i className='fa-solid fa-user  fa-sm me-1'></i>Your Profile</Link>
            </li>

          </ul> : null}


          <ul className="navbar-nav ms-auto mt-2 mt-lg-0">
            <li className="nav-item d-flex align-items-center">
              <i className='fab mx-2 fa-facebook'></i>
              <i className='fab mx-2 fa-twitter'></i>
              <i className='fab mx-2 fa-instagram'></i>
              <i className='fab mx-2 fa-tiktok'></i>
              <i className='fab mx-2 fa-linkedin'></i>
              <i className='fab mx-2 fa-youtube'></i>
            </li>

            {userData ==null ? <>
              <li className="nav-item">
                <Link className="nav-link" to="Login">Login</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="Register">Register</Link>
              </li></>
             :<>
                <li className="nav-item position-relative mx-2">
                  <Link className="nav-link px-2" to="wishList">
                    <i className='fa-solid fa-heart text-danger fa-lg'></i>
                    <span className='badge position-absolute top-0 end-0\  bg-main text-white'>{count}</span>
                  </Link>
                </li>
                <li className="nav-item position-relative ">
                  <Link className="nav-link px-2" to="Cart">
                    <i className='fas fa-shopping-cart fa-lg'></i>
                    <span className='badge position-absolute top-0 end-0\  bg-main text-white'>{numOfCartItems}</span>
                  </Link>
                </li>
                <li className="nav-item mx-3">
                  <Link className="nav-link" onClick={logOut} >Logout</Link>
                </li>
              </>

            }
          </ul>

        </div>
      </div>
    </nav>

  </>
}

