import React from 'react'
import style from './ResetPass.module.css'
import { useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import { useFormik } from 'formik'
import axios from 'axios'



export default function ResetPass() {
let navigate = useNavigate()
let valid = Yup.object({
  email:Yup.string().email("Enter valid email").required(),
  newPassword:Yup.string().matches(/^[A-Z][a-z0-9!@#$$%^&*()_-]{3,16}$/,"Enter valid password").required(),
})

let formik = useFormik({
  initialValues:{
        
    email:"",
    newPassword:"",
   
  },
  onSubmit:(values)=>{
    resetPassword(values)
  },
  validationSchema:valid

})

async function resetPassword(value){
  let {data}=await axios.put(`https://ecommerce.routemisr.com/api/v1/auth/resetPassword`,value)
  console.log(data);
  
  if (data.userToken) {
    navigate("/Login")
 }
}
  return <>
  <div className='p-4 my-4'>
  <form onSubmit={formik.handleSubmit}>

<div className="my-3">
 <label htmlFor='email'>email</label>
 <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='email' name='email' id='email' className='form-control mt-2 bg-transparent text-white'/>
 {formik.touched.email&&formik.errors.email? <p className='text-danger mt-2'>{formik.errors.email}</p>:""}
</div>
<div className="my-3">
 <label htmlFor='newPassword'>New password</label>
 <input onBlur={formik.handleBlur} onChange={formik.handleChange} type='password' name='newPassword' id='newPassword' className='form-control mt-2 bg-transparent text-white'/>
 {formik.touched.password&&formik.errors.password? <p className='text-danger mt-2'>{formik.errors.newPassword}</p>:""}


</div>


<button type='submit' className='btn bg-main text-white'>Reset Passwprd</button>


</form>
  </div>
  </>
}

